<template>
  <div class="register">
    <van-nav-bar
      title="注册"
      left-text="登录"
      right-text="关闭"
      @click-left="logins('Login')"
      @click-right="goMenu"
    />
    
    <div class="logo">
      <img src="../assets/images/a.png" class="auto-img"/>
    </div>

    <van-form class="form" @submit="register">
        <van-field
        type="text"
        v-model="userinfo.phone"
            label="手机号"
            placeholder="请输入11位手机号"
            left-icon="phone-o"
            maxlength='11'
        >
        </van-field>

          <van-field
          type="text"
            label="昵称"
            v-model="userinfo.nickName"
            placeholder="字母数字_组合(1-10位)"
            left-icon="user-o"
             maxlength="10"
        />
             <van-field
                :type="addNum?'text':'password'"
                label="密码"
                v-model="userinfo.password"
                placeholder="字母数字_组合(6-16位)"
                left-icon="lock"
                :right-icon="addNum?'eye-o':'closed-eye'"
                @click-right-icon="passWd"
                maxlength="16"
            >

             </van-field>

        <div class="sub_btn">
            <van-button round block type="info" native-type="submit">
            注册
            </van-button>
        </div>
   </van-form>

      <!-- <van-dialog title="阿里路亚">
      </van-dialog> -->
  </div>
</template>

<script>

  //导入验证表单模块
  import validForm from '../assets/js/validForm'

  export default {
      data(){
        return {
          userinfo: {
            phone: '',
            nickName: '',
            password: ''
          },
          addNum: false
        }
      },
      computed:{
       
      },
      methods:{
        logins(name){
           this.$router.push({name})
        },
        passWd(){
          this.addNum=!this.addNum;
        },
        //注册
        register(){
          
          
          if (validForm.validUserForm(this.userinfo, this)) {
            

        
            let data = Object.assign({}, this.userinfo);

            
            data.appkey = this.appkey;

            this.$toast.loading({
              message: '加载中...',
              forbidClick: true,

              
              duration:0
            });

           
            this.axios({
              method: 'POST',
              url: '/register',

             
              data
            }).then(result => {
              //关闭加载提示
              this.$toast.clear();
              this.$router.push({name: 'Login'})
              
            }).catch(err => {
              //关闭加载提示
              this.$toast.clear();
              
            })
          }
        },
         goMenu(){
          this.$router.push({name: 'Menu'});
        }
      }
  }
</script>

<style lang="less" scoped>
   .logo{
    width: 120px;
    height: 120px;
    margin: 50px auto;
  }
  .sub_btn{
    margin: 50px;
  }
</style>